:root {
    --gray: hsl(197, 10%, 45%);
    --middle-gray: hsl(240, 6%, 92%);
    --light-gray: hsl(240, 6%, 97%);
    --accent: hsl(197, 100%, 44%);
    --accent-hover: hsl(197, 100%, 40%);
    --accent-active: hsl(197, 100%, 35%);
    --focus-shadow: 0px 0px 2px var(--accent);
    --focus-border: solid 1px var(--accent);
    --focus-border-inactive: solid 1px transparent;
    --max-width: 1000px;
    --transparent-white: hsla(0, 100%, 100%, 1);
    --red: rgb(249, 73, 73);
    --green: hsl(170, 90%, 44%);
}

html {
    overflow-y: hidden;
    height: 100%;
}

body {
    color: var(--gray);
